const PACKAGE_NAME = '@wix/auto_sdk_stores_catalog-imports-v-3';

/**
 * Creates a CatalogImport for upsert product operations.
 *
 * Returns `catalogImport.id` that can be used in subsequent AddProductsToImport and SubmitCatalogImport calls.
 */
export function createCatalogImport(
  payload: object
): RequestOptionsFactory<any> {
  function __createCatalogImport({ host }: any) {
    const serializedData = transformPaths(payload, [
      {
        transformFn: transformSDKTimestampToRESTTimestamp,
        paths: [{ path: 'catalogImport.createdDate' }],
      },
    ]);
    const metadata = {
      entityFqdn: 'wix.stores.catalog.v3.catalog_import',
      method: 'POST' as any,
      methodFqn:
        'wix.stores.catalog.catalog_import.v3.CatalogImports.CreateCatalogImport',
      packageName: PACKAGE_NAME,
      migrationOptions: {
        optInTransformResponse: true,
      },
      url: resolveWixStoresCatalogCatalogImportV3CatalogImportsUrl({
        protoPath: '/v3/catalog-imports',
        data: serializedData,
        host,
      }),
      data: serializedData,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [{ path: 'catalogImport.createdDate' }],
          },
        ]),
    };

    return metadata;
  }

  return __createCatalogImport;
}

