/**
 * Submits all products in the catalog import for processing.
 * Once called, no more items can be added to this import session.
 */
export function submitCatalogImport(
  payload: object
): RequestOptionsFactory<any> {
  function __submitCatalogImport({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.stores.catalog.v3.catalog_import',
      method: 'POST' as any,
      methodFqn:
        'wix.stores.catalog.catalog_import.v3.CatalogImports.SubmitCatalogImport',
      packageName: PACKAGE_NAME,
      migrationOptions: {
        optInTransformResponse: true,
      },
      url: resolveWixStoresCatalogCatalogImportV3CatalogImportsUrl({
        protoPath: '/v3/catalog-imports/{catalogImportId}/submit',
        data: payload,
        host,
      }),
      data: payload,
    };

    return metadata;
  }

  return __submitCatalogImport;
}
